.btn {
  text-transform: none;
  color: var(--primery-text-color-teme-light);
  cursor: inherit;
  border: 2px solid rgba(0, 0, 0, 0);
  border-radius: 10px;
  outline: none;
  padding: 10px 32px;
  font-family: inherit;
  font-size: 1.4rem;
  font-weight: 600;
  line-height: 1.2;
  display: inline-block;
}

.btn--underlined {
  color: var(--default-button-color-teme);
  text-decoration: underline;
  transition: color .25s cubic-bezier(.4, 0, .2, 1);
}

.btn--underlined:hover {
  color: var(--hover-button-color-teme);
}

.btn--underlined:focus {
  color: var(--focus-button-color-teme);
}

.btn--underlined:disabled {
  color: var(--disabled-button-color-teme);
}

.btn--primary {
  background-color: var(--default-button-color-teme);
  transition: color .25s cubic-bezier(.4, 0, .2, 1), background-color .25s cubic-bezier(.4, 0, .2, 1);
}

.btn--primary:hover {
  background-color: var(--hover-button-color-teme);
}

.btn--primary:focus {
  color: var(--focus-button-color-teme);
  background-color: var(--hover-button-color-teme);
}

.btn--primary:disabled {
  background-color: var(--disabled-button-color-teme);
}

.btn--primary svg {
  fill: currentColor;
  pointer-events: none;
}

.btn--border {
  border-color: var(--default-button-color-teme);
  background-color: rgba(0, 0, 0, 0);
  transition: color .25s cubic-bezier(.4, 0, .2, 1), border-color .25s cubic-bezier(.4, 0, .2, 1);
}

.btn--border:hover {
  background-color: var(--hover-button-color-teme);
}

.btn--border:focus {
  color: var(--focus-button-color-teme);
  background-color: var(--hover-button-color-teme);
}

.btn--border:disabled {
  background-color: var(--disabled-button-color-teme);
}

.btn--border svg {
  fill: currentColor;
  pointer-events: none;
}

.backdrop {
  width: 100%;
  height: 100%;
  z-index: 30;
  opacity: 1;
  background-color: rgba(10, 10, 10, .5);
  transition: opacity .25s cubic-bezier(.4, 0, .2, 1), visibility .25s cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  top: 0;
  left: 0;
  overflow-y: scroll;
}

.backdrop.is-hidden {
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
}

@media screen and (max-width: 767px) {
