.modal {
  width: 280px;
  background: var(--secondary-text-color-teme-dark);
  border: 2px solid var(--third-text-color-teme-dark);
  border-radius: 10px;
  margin: 0 auto;
  padding: 28px;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
  transform: scale(1);
}

.backdrop.is-hidden .modal {
  transform: scale(1.1);
}

@media screen and (min-width: 768px) {
  .modal {
    width: 568px;
    padding: 32px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%)scale(1);
  }

  .backdrop.is-hidden .modal {
    transform: translate(-50%, -50%)scale(1.1);
  }
}

